<template>
  <div class="user">
    <div>id:{{ $route.params.id | first}}</div>
    <div>
      <span></span>|
      <span v-once></span>|
      <span v-text="d"></span>|
      <span v-html="e"></span>|
      <span v-if="a % 2">a  </span>
      <span v-else>a </span>|
      <span
        v-for="(v, i) in f"
        :key="i + 'f'"
      >{{v.a}}:{{i}}</span>|
      <span
        v-for="(v, i) in ff"
        :key="i + 'ff'"
      >{{v}}:{{i}}</span>|
      <span
        v-for="(v, i) in fff"
        :key="i + 'fff'"
      >{{v.a}}:{{i}}</span>|
      <span v-pre> {{a}}</span>|
      <span
        v-bind:todo="g"
        :style="{fontSize: '12px'}"
      >dom {{g}}</span>|
      <span style="font-size: 12px">inject  {{foo}}</span>|
    </div>
    <router-link :to="{ path: '/main/test/test1'}"></router-link>
    <div>stroe: {{s.index}} stroe.b:{{s.b.d}}</div>
    <div @click="changeText">: {{text | capitalize}} </div>
    <div>:{{maxh}}</div>
    <div @click="addIndex">storecommitstroe  action {{this.$store.getters.indexTodo}}</div>
    <div @click="dispatchIndex">stroe</div>
    <div
      @click="watchEvent"
      ref="a"
    >{{a}}</div>
    <div>
      <span></span>
      <input
        v-focus
        v-model="c"
      />
    </div>
    <div @click="show = !show">
      
      <span v-show='show'>{{show}}</span>|
      <transition
        name="fade"
        @after-enter="afterEnter"
      >
        <span
          v-show="show"
          ref='liner'
        ></span>
      </transition>
    </div>
    <you-too
      v-bind="$attrs"
      v-on:slotCreated="slotCreated"
    >
      <p>slot default</p>
      <p slot="a">slot 7654321</p>
      <p>slot default2</p>
      <div slot="b">slot 1234567</div>
    </you-too>
  </div>
</template>

<script>
import k from '../utils/index'
import YouToo from './d.vue'
export default {
  name: 'aTemplate',
  components: {
    YouToo
  },
  props: {

  },
  data () {
    return {
      s: this.$store.state,
      text: 'aeiou',
      show: true,
      h: 3,
      a: 1,
      b: { a: 1 },
      c: 1,
      d: 'v-text',
      e: '<span style = "color: red">2</span>',
      f: new Array(10).fill({ a: 1 }),
      ff: { a: 1, b: 2, c: 3, d: 4 },
      fff: k.randomString(10),
      g: 'todo'
    }
  },
  inject: ['foo'],
  methods: {
    changeText: function () {
      k.setImmediate(function () { console.log('setImmediate') })
      this.text += k.randomString(1)
    },
    addIndex: function () {
      this.s.index++
      this.$store.commit('', 2)
    },
    dispatchIndex: function () {
      this.$store.dispatch('todo')
    },
    afterEnter: function () {
      let liner = this.$refs.liner
      let html = this.$refs.liner.innerHTML
      html += k.randomString(1)
      liner.innerHTML = html
    },
    slotCreated: function () {
      // console.log('')
    },
    watchEvent: function () {
      this.a++
      this.b.a++
      console.log(this.$refs.a.innerHTML)
      this.$nextTick(function () {
        console.log(this.$refs.a.innerHTML)
      })
    }
  },
  computed: {
    maxh: function () {
      return this.h * 2
    }
  },
  directives: {
    focus: {
      inserted: function (el) {
        el.focus()
      }
    }
  },
  filters: {
    capitalize: function (value) {
      if (!value) return ''
      value = value.toString()
      return value.charAt(0).toUpperCase() + value.slice(1)
    }
  },
  watch: {
    a: function (val, oldVal) {
      console.log('new: %s, old: %s', val, oldVal)
    },
    b: {
      handler: function (val, oldVal) {
        console.log('new: %s, old: %s', val, oldVal)
      },
      deep: true
    }
  },
  beforeCreate: function () {
    // console.log('')
  },
  created: function () {
    this.$on('hello', () => console.log('hello'))
    this.$once('once', () => console.log('once'))
    this.$off('hello')
    this.$emit('once')
    // console.log('')
  },
  beforeMount: function () {
    // console.log('')
  },
  mounted: function () {
    // console.log('')
    console.log(this)
    // console.log(this.$parent)  //
    // console.log(this.$children)  //
    // console.log(this.$root)  // 
    // console.log(this.$refs)  // 
    // console.log(this.$store)  // store
    // console.log(this.$attrs)  // 
    // console.log(this._props)  // props
  },
  beforeUpdate: function () {
    // console.log('')
  },
  updated: function () {
    // console.log('')
  },
  activated: function () {
    // console.log('keep-alive')
  },
  deactivated: function () {
    // console.log('keep-alive')
  },
  beforeDestroy: function () {
    // console.log('')
  },
  destroyed: function () {
    // console.log('')
  },
  errorCaptured: function () {
    // console.log('')
  }
}
</script>

